.home-container {
  row-gap: 7rem;
}
.home-content {
  grid-template-columns: 116px repeat(2, 1fr);
  padding-top: 5.5rem; /* I need here some chenge */
  column-gap: 2rem;
  align-items: center;
}

.home-social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}

.home-social-icon {
  font-size: 1.25rem;
  color: var(--title-color);
}
.home-social-icon:hover {
  color: var(--title-color-dark);
}

.home-title {
  /* font-size:  2.75rem; */
  font-size: var(--big-font-size);
  margin-bottom: var(--mb-0-25);
}
/* .home-hand {
  width: 38px;
  height: 38px;
  margin-left: 0.4rem;
} */

.home-subtitle {
  position: relative;
  font-size: var(--h2-font-size);
  padding-left: 3rem;

  font-weight: var(--font-normal);
  margin-bottom: var(--mb-1);
}
